.coments-area{
    .single-comments{
        @media #{$xs}{
           margin-left: 0;
        }
        .comments-items{
            @include flexbox();
            // @include align-items(center);
            @media #{$md}{
                @include flex-wrap(wrap);
            }
            @media #{$sm}{
                @include flex-wrap(wrap);
            }
            @media #{$xs}{
                @include flex-wrap(wrap);
            }
        }
        .comments-img{
            img {
                margin-right: 32px;
                @media #{$md}{
                    margin-bottom: 20px;
                }
                @media #{$sm}{
                    margin-bottom: 20px;
                }
                @media #{$xs}{
                    margin-bottom: 20px;
                }
            }
        } 
        .comments-tittle{
            @media #{$xs}{
                padding: 14px;
            }
            @media #{$sm}{
                padding: 14px;
            }
            span{
                font-size: 14px;
                color: #BBBBBB;
                text-transform: uppercase;
                margin-bottom: 17px;
                display: block;
            }
            a{
                h4{
                    color: #140C40;
                    font-size: 25px;
                    font-weight: 400;
                    @include transition(.4s);
                    margin-bottom: 16px;
                    display: inline-block;
                    &:hover{
                        color:#8b92dd;
                    }
                    @media #{$lg}{
                        font-size: 22px;
                        font-weight: 500;
                    }
                }
                .author {
                    display: inline-block !important;
                    padding: 5px 22px;
                    border: 1px solid #bbbb;
                    margin-left: 28px;
                    border-radius: 30px;
                }
            }
            p{
                font-size: 16px;
                color: #3B4855;
                margin-bottom: 0px;
            }
        }
        .comments-tittle2{
            @media #{$md}{
                padding-top: 25px;
            }
        }
    } 
}

